import React, { createContext, useContext, useMemo, useRef, useState } from "react";

const ArcadeCtx = createContext(null);

export function ArcadeProvider({ children }) {
  const [score, setScore] = useState(0);

  // Last snapshot of counts; used to award points only when counts drop
  const last = useRef({
    red: 0, yellow: 0, blue: 0, hard: 0,
    belowTarget: false,
    hasText: false
  });

  // Prevent bonus on the very first metrics pass
  const firstSeen = useRef(false);

  const addPoints = (n) => setScore((s) => Math.max(0, s + n));

  const resetScore = () => {
    setScore(0);
    last.current = { red: 0, yellow: 0, blue: 0, hard: 0, belowTarget: false, hasText: false };
    firstSeen.current = false;
  };

  /**
   * Called by the editor (debounced) with:
   * { red, yellow, blue, hard, grade, belowTarget, hasText }
   */
  const reportMetrics = (m) => {
    // First pass: seed state so we don't award instantly
    if (!firstSeen.current) {
      last.current = { ...m };
      firstSeen.current = true;
      return;
    }

    const prev = last.current;

    const redRemoved  = Math.max(prev.red    - m.red,    0);
    const yelRemoved  = Math.max(prev.yellow - m.yellow, 0);
    const blueRemoved = Math.max(prev.blue   - m.blue,   0);
    const hardRemoved = Math.max(prev.hard   - m.hard,   0);

    if (redRemoved)  addPoints(redRemoved  * 100);
    if (yelRemoved)  addPoints(yelRemoved  * 50);
    if (blueRemoved) addPoints(blueRemoved * 25);
    if (hardRemoved) addPoints(hardRemoved * 25);

    // One-time bonus the first time we cross into the target band
    if (!prev.belowTarget && m.belowTarget && m.hasText) {
      addPoints(1000);
    }

    last.current = { ...m };
  };

  const value = useMemo(() => ({
    score,
    resetScore,
    reportMetrics,
  }), [score]);

  return <ArcadeCtx.Provider value={value}>{children}</ArcadeCtx.Provider>;
}

export function useArcade() {
  const ctx = useContext(ArcadeCtx);
  if (!ctx) throw new Error("useArcade must be used within ArcadeProvider");
  return ctx;
}
